<template>
<div v-if="ready" style="height:87.5vh">
    <div id='top_page' class="row" >
    <!--right-->
    <div data-app class="col-lg-12">                   
            <div class="form-group" style='margin-bottom:0.6em'>
            <!--live search-->           
                <div style="display:flex;flex-direction:row;margin-left:1em;padding-left:0" class="col-12">
                    <div class="col-3" style='padding-left:0;padding-right:0'>
                        <div class="col-12" style='padding-left:0'>
                            <label>Select Department</label>
                            <select v-model="dept" id='dept_select' class="form-control" v-if="dept_ready" @change="new_list" style='width:100%'>
                                <option v-for="dep in dept_list " :value="dep.department">{{ dep.dept_display }}</option>
                            </select>
                        </div>
                         <div class='col-12' style="display:flex;flex-direction:row;margin-top:0.3em;padding-left:0">
                            <div class="col-4" style='padding-left:0;padding-right:0.125rem;'>
                                <button type="button" class="form-control" @click="newplan" style="margin-top:0.4em">New</button>  
                            </div>
                            <!--<button type="button" class="form-control" style="margin-top:0.4em;width:32.5%">Import</button>-->
                            <div class="col-4" style='padding-left:0.125rem;padding-right:0.125rem;'>
                                <select v-model="status" class="form-control" v-if="dept_ready" @change="new_list" style="margin-top:0.45em">
                                    <option v-for="s in stl ">{{ s }}</option>
                                </select>
                            </div>
                            <div class="col-4" style='padding-left:0.125rem;padding-right:0;'>
                                <select v-model="cat" class="form-control" v-if="dept_ready" @change="new_list" style="margin-top:0.45em">
                                    <option v-for="c in cat_type ">{{ c }}</option>
                                </select>
                            </div>
                        </div>                                                          
                    </div>
                    <div class='col-5' style="padding-right:0;margin-left:auto">
                        <div class='col-12'>
                            <label>Filter(s)</label>
                            <input type='text' class="form-control" placeholder="Search Record" v-model="key" @keyup="search_list">
                        </div>
                        <div class="flex col-12" style='margin-top:0.6em'>       
                            <div class="flex col-6 pl0">                
                                <input type="number" v-model="st" class="form-control" @change="update_period"/>
                            </div>
                            <div class="flex col-6 pr0">  
                                <input type="number" v-model="ed" class="form-control" readonly/>
                            </div>
                        </div>   
                    </div>
                </div>
            </div>           
            <div data-app class="col-12">                   
                <div class="form-group">
                    <div id='listWrapper' v-if="list_ready" v-html="printlist" class='col-12 px0'>
                        {{ printlist }}
                    </div>
                    <input type="hidden" id='v_reimb_f_proc' @click="view_form"/>
                    <input type="hidden" id='edit_proc' @click="edit_proc_record"/>
                    <input type="hidden" id='view_proc' @click="view_proc_record"/>
                    <input type="hidden" id='view_proc_comp2' @click="view_proc_comp2"/>
                    <input type="hidden" id='return_info' @click="return_info"/>
                    <input type="hidden" id='fill_return' @click="fill_return_record"/>
                    <input type="hidden" id='reject_proc_record' @click="reject_proc_record"/>
                    <input type="hidden" id='cancel_proc_record' @click="cancel_proc_record"/>                    
                    <input type="hidden" id='goreimb' @click="go_reimb"/>
                    <input type="hidden" id='trigger_view_reimb_recur' @click="view_reimb_records"/>
                    <input type="hidden" id='recur_reimb' @click="recur_reimb"/>   
                    <input type="hidden" id='recur_reimb_setup' @click="recur_reimb_setup"/>                 
                    <input type="hidden" id='sign_code' @click="sign_code"/>  
                    <input type="hidden" id='tender_meeting_setup' @click="tender_meeting_setup"/>  
                    <input type="hidden" id='tender_meeting_mat' @click="tender_meeting_mat"/>  
                    <input type="hidden" id='retender' @click="retender"/>  
                </div>
            </div>
            <!--list-->
    </div>
    </div>
    <div class="corner_nav">
        <div class="col-12">
            <div class="corner_item" @click="scrollTop">
                <i class="fa fa-2x fa-arrow-up" style='color:rgba(255,255,255,0.9)'></i>
            </div>
        </div>
    </div>
</div>
</template>
<script>
export default {
    data(){
        return{
            list_ready:false,
            ready:false,
            cat:'All',
            tag:'',
            dept_list:[],
            dept:'',
            dept_ready:false,
            assetlist:[],
            st:'',
            ed:'',
            insert_fund_name:'',
            fund_current : 'Regular',
            fund_type:['Regular','Special'],
            cat_type:['All','5,001 ~ 50,000','50,001 ~ 200,000','>200,000'],
            printlist:'',
            key:'',
            user_check:'',
            status:'All',
            stl:['All','Preparing','Rejected'],
        }
    },
async created(){
    await axios.get('Procurement/shared_backend/check_user_setup.php')
    .then(
        (response)=>{
            if(!(response.data=='bypass')){
                this.user_check = response.data[0].signed;
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
            }
        }
    )
    await axios.get('Procurement/department/backend/dept_member_sub.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                this.dept = this.dept_list[0].department;
                this.dept_ready = true;
                var check_previos = document.getElementById('previous_selected').value;
                if(!(check_previos=='none')){
                    this.dept = check_previos;
                }
            }
        );
    
    await axios.get('Procurement/shared_backend/default_period.php')
        .then(
            (response)=>{
                var st = response.data.st.split("-");
                var ed = response.data.ed.split("-");
                this.st = st[0];
                this.ed = parseInt(ed[0]);
            }
        )     
    var formData = new FormData();
    formData.append('cat',this.cat);
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    formData.append('stat',this.status);
    await axios.post('Procurement/procurement/backend/printlist_proc.php',formData)
        .then(
            (response)=>{
                this.printlist = response.data;
                this.list_ready = true;
            }
        )  
    var check_entry = document.getElementById('entry').value;
    if(check_entry=='proc_entry'){
        this.key = document.getElementById('tmp_id').value;
        this.dept = document.getElementById('tmp_id_2').value;
        document.getElementById('entry').value='';
        var formData = new FormData();
        formData.append('cat',this.cat);
        formData.append('dept',this.dept);
        formData.append('st',this.st);
        formData.append('ed',this.ed);
        formData.append('key',this.key);
        formData.append('stat',this.status);
        axios.post('Procurement/procurement/backend/printlist_proc_live.php',formData)
            .then(
                (response)=>{
                    this.printlist = response.data;
                    this.list_ready = true;
                    this.$forceUpdate();
                }
        )
    }    
    this.ready = true;
},
mounted(){
    window.addEventListener('focus', this.focus_load);
},
beforeDestroy() {
    document.getElementById('proc_element').value = '';
    window.removeEventListener('focus', this.focus_load);
},
methods:{
    scrollTop(){
        document.getElementById('top_page').scrollIntoView();
    },
    async update_period(){
        this.ed = parseInt(this.st)+1;
        this.$forceUpdate();
        await this.new_list();
    },
    view_proc_comp2(){
        document.getElementById('st_tmp').value = this.st+'-08';
        document.getElementById('ed_tmp').value = this.ed+'-07';
        document.getElementById('doc_type').value = 'Procurement';
        this.$router.push('../wrapper/form_view');
    },
    resume_row(id){
        var new_id = id.substring(9);
        var current = document.getElementById(new_id).style.display;
        var target = 'expand_'+new_id;
        if(current == 'none'){  //EXPAND
            document.getElementById(new_id).style.display='';
            document.getElementById('icon_'+new_id).style.transform = "rotate(-180deg)";
        } else {    //COLLAPSE
            document.getElementById(new_id).style.display='none';
            document.getElementById('icon_'+new_id).style.transform = "rotate(0deg)";
        }
    },
    async focus_load(){
        var scroll_element = document.getElementById('content-panel');
        var top = scroll_element.scrollTop;
        var proc_array = document.getElementById('proc_element').value;
        if(proc_array!=''){
            var array = JSON.parse(proc_array);
        } 
        this.printlist = '';
        this.list_ready=false;
        var formData = new FormData();
            formData.append('cat',this.cat);
            formData.append('dept',this.dept);
            formData.append('key',this.key);
            formData.append('st',this.st);
            formData.append('ed',this.ed);
            formData.append('stat',this.status);           
        //setTimeout(async function(){                      
            await axios.post('Procurement/procurement/backend/printlist_proc_live.php',formData)
                .then(
                    (response)=>{                       
                        this.printlist = response.data;
                        this.list_ready = true;
                    }
                )  
        //}, 500);
        this.$forceUpdate();   
        var val='';
        if(proc_array!=''){
            for(var a = 0; a<array.length; a++){
                val = array[a];
                this.resume_row(val);
            }
        }
        scroll_element.scrollTop = top;
    },
    search_list(event){
    var formData = new FormData();
    formData.append('cat',this.cat);
    formData.append('dept',this.dept);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    formData.append('key',this.key);
    formData.append('stat',this.status);
    axios.post('Procurement/procurement/backend/printlist_proc_live.php',formData)
        .then(
            (response)=>{
                this.printlist = response.data;
            }
        )
    },
    new_list(event){
    var formData = new FormData();
    formData.append('cat',this.cat);
    formData.append('dept',this.dept);
    formData.append('key',this.key);
    formData.append('st',this.st);
    formData.append('ed',this.ed);
    formData.append('stat',this.status);
    axios.post('Procurement/procurement/backend/printlist_proc_live.php',formData)
        .then(
            (response)=>{
                this.printlist = response.data;
            }
        )   
    },
    view_form(){
        var tag = document.getElementById('tmp_id').value;
        console.log(tag);
        var path = "src/Procurement/reimbursement/backend/view_form_app.php?tag="+tag+"&st="+this.st+"-08&ed="+this.ed+'-07';
        window.open(path, "_blank");
    },
    newplan(){
        this.$router.push('new_proc');
    },
    edit_proc_record(){
        document.getElementById('st_tmp').value=this.st+'-08';
        document.getElementById('ed_tmp').value=this.ed+'-07';
        this.$router.push('edit_proc');
    },
    view_proc_record(){
        document.getElementById('st_tmp').value=this.st+'-08';
        document.getElementById('ed_tmp').value=this.ed+'-07';
        this.$router.push('view_only');
    },
    fill_return_record(){
        document.getElementById('st_tmp').value=this.st+'-08';
        document.getElementById('ed_tmp').value=this.ed+'-07';
        this.$router.push('fill_return');
    },
    return_info(){
        document.getElementById('st_tmp').value=this.st+'-08';
        document.getElementById('ed_tmp').value=this.ed+'-07';
        this.$router.push('return_info');
    },
    cancel_proc_record(){
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('tag',id);
        if(confirm('Confirm Cancelling Record : '+id+'?')==true){
        axios.post('Procurement/procurement/backend/cancel.php',formData)
        .then(
            (response)=>{
                location.reload();
            }
        )
        }   
    },
    reject_proc_record(){        
        var id = document.getElementById('tmp_id').value;
        var formData = new FormData();
        formData.append('dept',this.dept);
        formData.append('tag',id);
        if(confirm('Confirm Rejecting Record : '+id+'?')==true){
        axios.post('Procurement/procurement/backend/reject.php',formData)
        .then(
            (response)=>{
                location.reload();
            }
        )
        }
    },
    go_reimb(){
        this.$router.push('../reimbursement/proc_reimb');
    },
    view_form(){
        var tag = document.getElementById('tmp_id').value;
        var path = "src/Procurement/reimbursement/backend/view_form.php?tag="+tag+"&st="+this.st+"-08&ed="+this.ed+'-07';
        window.open(path, "_blank");
    },
    view_reimb_records(){
        this.$router.push('../reimbursement/reimb_records');
    },
    recur_reimb(){
        this.$router.push('../reimbursement/recur_setup');
    },
    recur_reimb_setup(){
        this.$router.push('../reimbursement/recur_setup');
    },
    sign_code(){
        document.getElementById('st_tmp').value=this.st+'-08';
        document.getElementById('ed_tmp').value=this.ed+'-07';
        this.$router.push('sign_code');
    },
    tender_meeting_setup(){
        this.$router.push('tender_meeting_setup');
    },
    tender_meeting_mat(){
        this.$router.push('tender_meeting_mat');
    },
    retender(){
        this.$router.push('retender');
    }
}
}
</script>
<style scoped>
.px0{
    padding-left:0;
    padding-right: 0;
}
.pl0{
    padding-left:0;
}
.pr0{
    padding-right:0;
}
</style>


